@import "./utils.scss";
@import "../styles/App.scss";
.order {
  width: 100%;
  height: 100%;
  overflow: hidden;
  .almighty {
    @include almighty;
    h1 {
      cursor: default;
      position: relative;
      line-height: 3;
      @include mobile {
        line-height: 1;
        margin-top: 15%;
      }
      &::after {
        @include mobile {
          top: -11vmax;
          right: initial;
          background-position: center;
        }
        z-index: -1;
        position: absolute;
        top: -7vmax;
        right: 4vmax;
        width: 100%;
        height: 100%;
        content: "";
        display: block;
        background: url("../../public/images/logo.png") no-repeat;
        background-size: 20%;
        background-position: right;
      }
    }
  }

  .menu {
    width: 90%;
    margin: auto;
    display: flex;
    align-items: center;
    &__text {
      font-family: "Poppins", sans-serif;
      font-size: 4vmax;
      width: fit-content;
      margin-right: 3%;
      line-height: 1.5;
    }
    span {
      &:last-child {
        width: 100%;
        height: 4px;
        background: rgb(255, 92, 92);
      }
    }
  }
}
#menuWrap {
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: #fffcf5;
  #menuSlide {
    display: grid;
    grid-template-columns: repeat(10, 30vmax);
    grid-template-rows: 40vmax;
    padding: 5vmax 0;
    .foods {
      cursor: pointer;
      width: 100%;
      height: 80%;
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      align-items: center;
      &:hover {
        .orderMsg {
          opacity: 1;
        }
      }
      a {
        width: 50%;
        height: 80%;
        position: absolute;
      }
      svg {
        z-index: -1;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        // ellipse {
        //   mask: url("data:image/png;base64,");
        //   mask-size: 500%;
        // }
      }
      &:nth-child(2n) {
        align-self: flex-end;
      }
      &name {
        text-align: center;
        font-size: 3.5vmax;
        font-family: "PoetsenOne-Regular";
        color: transparent;
        -webkit-text-stroke: 1px black;
        width: 60%;
      }
      .imge {
        width: 60%;
        height: 20vmax;
        object-fit: contain;
      }
      .orderMsg {
        transition: opacity 0.5s ease-in-out;
        opacity: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        z-index: 50;
        height: 10vmax;
        width: 10vmax;
        background-color: rgba(255, 255, 255, 0.678);
        color: red;
        position: absolute;
        > div {
          transform: rotate(180deg);
          span {
            font-size: 4vmax;
          }
        }
        span {
          font-size: 1vmax;
        }
      }
    }
  }
}
footer {
  height: 50vh;
  width: 100%;
  overflow: hidden;
  position: relative;
  z-index: -1;
  .footer__text {
    margin: auto;
    width: 60%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 7vmax;
    font-family: "PoetsenOne-Regular";
    span {
      word-break: break-all;
      &:first-child {
        color: red;
        align-self: flex-start;
      }
      &:last-child {
        color: black;
        align-self: flex-end;
      }
    }
  }
  .footer__logo {
    z-index: -1;
    width: 8%;
    height: 8%;
    background: url("../../public/images/logo.png") no-repeat;
    background-size: contain;
    background-position: center;
    position: absolute;
  }
}
@keyframes falling {
  0% {
    transform: translateY(-120%);
  }

  100% {
    transform: translateY(600%);
  }
}