nav {
  z-index: 100;
  position: fixed;
  top: 0;
  height: 5vmax;
  width: 100%;
  .navWrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: auto;
    height: 100%;
    width: 95%;
    position: relative;
    .nav__open {
      cursor: pointer;
      width: 30%;
      display: none;
      justify-content: space-between;
      align-items: center;
      position: absolute;
      opacity: 0;
      span {
        font-size: 3vmax;
        color: var(--orange);
      }
      div {
        cursor: pointer;
        font-size: 1.5vmax;
      }
    }
    .nav__close {
      font-size: 3vmax;
      color: var(--orange);
      cursor: pointer;
    }
    .cartbtn {
      cursor: pointer;
      font-weight: 600;
      font-size: 2vmax;
      b {
        font-size: 1.5vmax;
      }
    }
  }
}
