.food {
  height: 40vmax;
  width: 100%;
  display: flex;
  padding: 5vmax 0;
  &__wrap {
    height: 100%;
    width: 100%;
    display: flex;
    .food__pic,
    .food__info {
      height: 100%;
    }
  }
  &__pic {
    display: grid;
    place-items: center;
    width: 40%;
    background: url("../../public/images/cardBg.png") no-repeat;
    background-size: contain;
    background-position: -10vmax;
    img {
      object-fit: contain;
      height: 30vmax;
      width: 100%;
    }
  }
  &__info {
    width: 60%;
    height: 80%;
    > div {
      margin: auto;
      display: flex;
      width: 80%;
      justify-content: center;
      align-items: baseline;
      h1 {
        text-align: center;
        margin-right: 3%;
        font-size: 6vmax;
        font-family: "PoetsenOne-Regular";
        color: transparent;
        -webkit-text-stroke: 1px black;
      }
      .price {
        font-size: 2vmax;
        font-family: "Poppins", sans-serif;
      }
    }
    #form {
      margin: auto;
      margin-top: 5%;
      display: flex;
      flex-direction: column;
      align-items: center;
      height: 65%;
      width: 90%;
      h6 {
        width: 100%;
        font-weight: 500;
        text-transform: uppercase;
        font-size: 1.1vmax;
        &.prevent {
          display: none;
        }
      }
      .toppings {
        border: 1px solid black;
        border-radius: 50px;
        margin-top: 2%;
        position: relative;
        font-family: "Poppins", sans-serif;
        display: flex;
        width: 100%;
        height: 60%;
        overflow-x: scroll;
        &::-webkit-scrollbar {
          display: none;
        }
        &.prevent {
          display: none;
          pointer-events: none;
        }
        div {
          display: flex;
          align-items: center;
          width: 100%;
          height: 100%;
          margin: 0 3%;
          label {
            cursor: pointer;
            color: #000000;
            font-size: 1vmax;
            text-transform: capitalize;
            text-align: center;
            line-height: 2;
            background-position: center 65%;
            background-size: 45%;
            background-repeat: no-repeat;
            &::after {
              display: block;
              width: 7vw;
              height: 7vw;
              border-radius: 50%;
              border: 2px solid #a0a0a0;
              content: "";
            }
          }
          input {
            width: 0px;
            height: 0px;
            padding: 0px;
            margin: 0px;
            display: none;
            &:checked + label {
              &::after {
                color: rgba(255, 122, 0, 1);
                border: 2.5px solid rgba(255, 122, 0, 1);
                box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.25);
              }
            }
          }
        }
      }
      .quantityBox {
        display: flex;
        text-align: center;
        width: 30%;
        height: auto;
        #qty {
          position: relative;

          &.qty {
            &::after {
              content: "";
              display: block;
              position: absolute;
              top: 50%;
              transform: translateY(-50%);
              width: 100%;
              height: 36%;
              background-color: #f5aa1f;
              opacity: 0.6;
              z-index: -1;
            }
          }
        }
        span {
          width: 100%;
          line-height: 2;
          font-size: 2.5vmax;
          cursor: pointer;
        }
      }
      button {
        cursor: pointer;
        font-family: "Poppins", sans-serif;
        padding: 0 2%;
        text-transform: uppercase;
        box-shadow: 0px 10px 0px #000000;
        font-weight: 600;
        font-size: 1.5vmax;
        line-height: 2;
        background: #fff;
        border-radius: 50px;
      }
    }
  }
}