.cart {
  position: fixed;
  top: 0;
  right: -100vw;
  width: 100vw;
  height: 100vh;
  display: flex;
  &bg {
    opacity: 0;
    width: 50%;
    height: 100%;
    background-color: #0c0c0c4a;
  }
  &__wrap {
    text-align: center;
    width: 50%;
    height: 100%;
    background: #f5f5e6;
    overflow-y: scroll;
    &::-webkit-scrollbar {
      display: none;
    }
    h1 {
      position: relative;
      text-align: center;
      line-height: 2;
      font-size: 3vmax;
      font-weight: bold;
      border-bottom: 3px dashed rgba(0, 0, 0, 0.3);
      span {
        cursor: pointer;
        position: absolute;
        left: 3%;
        transform: rotate(180deg);
      }
    }
    .orderYet {
      font-weight: 500;
      font-size: 3vmax;
      margin: 10% 5%;
    }

    .reciptWrap {
      width: 100%;
      margin: auto;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      .myFood {
        user-select: none;
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 90%;
        border-bottom: 1px solid rgba(0, 0, 0, 0.3);
        .index {
          line-height: 5;
          color: red;
          font-weight: bold;
        }
        div {
          width: 50%;
          text-align: left;
          h2 {
            margin-bottom: 5%;
          }
        }
        .qty,
        .price {
          text-align: right;
          width: auto;
          line-height: 1.5;
        }
        .topping {
          font-size: 0.8vmax;
        }
        .delete {
          cursor: pointer;
          transition: 0.5s ease;
          width: 5%;
          height: auto;
          &:hover {
            transform: scale(1.2);
          }
        }
      }
    }
    .total {
      text-align: center;
      width: 90%;
      margin: auto;
      line-height: 2;
      font-size: 2vmax;
      font-weight: bold;
    }
    button {
      margin-top: 5%;
      cursor: pointer;
      font-family: "Poppins", sans-serif;
      padding: 0 2%;
      text-transform: uppercase;
      box-shadow: 0px 10px 0px #000000;
      font-weight: 600;
      font-size: 1.5vmax;
      line-height: 2;
      background: #fff;
      border-radius: 50px;
    }
  }
}
