@import "./utils.scss";
@import "../styles/App.scss";
.store {
  font-family: "Poppins", sans-serif;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  background: url("../../public/images/bg-orange.png") no-repeat;
  background-position: right center;
  background-size: 60% 100%;
  position: relative;
  .almighty {
    @include almighty;
  }
  &__imgs {
    height: 70%;
    width: 100%;
    position: absolute;
    bottom: 0;
    div {
      position: absolute;
      display: grid;
      img {
        opacity: 0;
        width: 80%;
        height: 100%;
        object-fit: contain;
      }
      &:first-child {
        @include tablet {
          width: 40%;
        }
        width: 30%;
        height: 80%;
        background: url("../../public/images/foodbg2.png") no-repeat;
        background-position: center;
        background-size: contain;
        right: 0;
        justify-items: end;
      }
      &:last-child {
        @include tablet {
          width: 40%;
        }
        width: 30%;
        height: 70%;
        background: url("../../public/images/foodbg1.png") no-repeat;
        background-position: center;
        background-size: contain;
        left: 0;
        bottom: 0;
        justify-items: center;
      }
    }
  }
  .selectForm {
    @include tablet {
      font-size: 2.5vmax;
    }
    font-size: 1.5vmax;
    position: absolute;
    bottom: 0;
    height: 50%;
    margin: auto;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    button {
      cursor: pointer;
      font-family: "Poppins", sans-serif;
      @include tablet {
        font-size: 2.5vmax;
      }
      padding: 0 2%;
      text-transform: uppercase;
      box-shadow: 0px 10px 0px #000000;
      font-weight: 600;
      font-size: 1.5vmax;
      line-height: 2;
      background: #fef0ca;
      border: none;
      border-radius: 50px;
    }
  }
}