:root {
  --yellow: #ffd94a;
  --orange: #ff9330;
  --green: #7fd1ae;
}
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap");

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
  background: #fffcf5;
  font-family: "Poppins", sans-serif;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
html::-webkit-scrollbar {
  display: none;
}
@mixin almighty {
  display: flex;
  width: 100%;
  height: 45%;
  text-align: center;
  flex-direction: column;
  justify-content: flex-end;
  h1 {
    width: fit-content;
    margin: 0 auto;
    z-index: 5;
    font-size: 8vmax;
    font-weight: 700;
  }
  h3 {
    width: fit-content;
    margin: 0 auto;
    position: relative;
    line-height: 2;
    font-size: 4vmax;
    font-weight: 700;
    color: red;
    &::after {
      position: absolute;
      top: -4.5vmax;
      width: 100%;
      height: 100%;
      content: "";
      display: block;
      background: url("../../public/images/logo.png") no-repeat;
      background-size: 80%;
      background-position: center;
    }
  }
}
